.home {
  height: 600px;
  display: flex;
  &__left {
    width: 100%;
    padding: 80px 40px;
    background-color: #06a763;
    .infos {
      color: #ffffff;
      margin-bottom: 50px;
    }
    .contacts {
      display: table;
      margin-bottom: 50px;
    }
    h1 {
      font-weight: 700;
      text-shadow: 2px 2px 4px rgb(0 0 0 / 29%);
    }
    h3 {
      font-weight: 400;
      text-shadow: 2px 2px 4px rgb(0 0 0 / 29%);
    }
    li {
      margin-bottom: 9px;
      display: flex;
      align-items: center;
      i.icon {
        width: 24px;
        height: 24px;
        margin-right: 24px;
      }
      .content {
        color: #000000;
        font-weight: 400;
        text-decoration: none;
      }
    }
    .links {
      i.icon {
        width: 54px;
        height: 54px;
        margin-right: 27px;
        border-radius: 50%;
        border: 1px solid #005a35;
        &:hover {
          box-shadow: 0 5px 11px 0 rgb(0 0 0 / 18%), 0 4px 15px 0 rgb(0 0 0 / 15%);
        }
      }
    }
    i.icon {
      overflow: hidden;
      display: inline-block;
    }
    i.icon.phone {
      background: url(../assets/icons/phone.svg) center center no-repeat;
    }
    i.icon.email {
      background: url(../assets/icons/email.svg) center center no-repeat;
    }
    i.icon.skype {
      background: url(../assets/icons/skype.svg) center center no-repeat;
    }
    i.icon.internet {
      background: url(../assets/icons/internet.svg) center center no-repeat;
    }
    i.icon.location {
      background: url(../assets/icons/location.svg) center center no-repeat;
    }
    i.icon.facebook {
      background: url(../assets/icons/facebook.svg) center center no-repeat;
    }
    i.icon.github {
      background: url(../assets/icons/github.svg) center center no-repeat;
    }
    i.icon.gitlab {
      background: url(../assets/icons/gitlab.svg) #ffffff8e center center no-repeat;
    }
  }
  &__right {
    position: relative;
    padding: 0 10px 0 110px;
    background-color: #ffffff;
    .slant {
      top: 0;
      left: 0;
      position: absolute;
      border-style: solid;
      border-width: 600px 0 0 200px;
      border-color: rgba(223, 50, 50, 0) transparent transparent #06a763;
    }
    .img-avt {
      width: 400px;
      height: 600px;
    }
  }
}

@media (max-width: 992px) {
  .home {
    position: relative;
    &__left {
      .infos {
        text-align: center;
      }
      .contacts {
        margin: 0 auto 50px auto;
      }
      .links {
        text-align: center;
      }
    }
    &__right {
      position: absolute;
      top: 0;
      left: 50%;
      padding: 0;
      border-radius: 50%;
      transform: translate(-50%, -215px);
      width: 300px;
      overflow: hidden;
      text-align: center;
      .slant {
        display: none;
      }
      .img-avt {
        width: 200px;
        height: 300px;
      }
    }
  }
}
