.about {
  &__content {
    padding: 40px 40px 20px 40px;
  }
  &__action {
    padding: 40px;
    border-top: 1px solid rgba(160, 160, 160, 0.2);
    &__btns {
      text-align: center;
      .btn {
        color: #fff;
        min-width: 180px;
        border-radius: 2px;
        margin: 7px 7px;
        padding: 10px 20px;
        background: #06a763;
        user-select: none;
        text-transform: uppercase;
        text-decoration: none;
        display: inline-block;
        &:hover {
          font-weight: bold;
          box-shadow: 0 5px 11px 0 rgb(0 0 0 / 18%), 0 4px 15px 0 rgb(0 0 0 / 15%);
        }
      }
    }
  }
}
