.app {
  header {
    position: relative;
    width: 100%;
    height: 444px;
    display: block;
    background: url(./assets/imgs/header.webp) no-repeat;
    background-position: center -333px;
    background-attachment: fixed;
    background-size: cover;
  }
  main {
    position: relative;
    padding: 0 15px;
    margin: -222px auto 0 auto;
    section {
      padding: 25px 0;
    }
  }
  footer {
    position: relative;
    height: 44px;
  }
}

@media (min-width: 768px) {
  .app {
    main {
      width: 750px;
    }
  }
}

@media (min-width: 992px) {
  .app {
    main {
      width: 970px;
    }
  }
}
@media (min-width: 1200px) {
  .app {
    main {
      width: 1170px;
    }
  }
}
