.loading {
  width: 100%;
  height: 100%;
  min-width: 100vw;
  min-height: 100vh;
  position: fixed;
  z-index: 5001;
  background-color: #06a763;
  top: 0;
  .loading-center {
    width: 100%;
    height: 100%;
    position: relative;
    .loading-center-absolute {
      top: 50%;
      left: 50%;
      width: 300px;
      height: 300px;
      position: absolute;
      transform: translate(-50%, -50%);
      .box-holder {
        text-align: center;
        animation: bounceInDown 0.8s both;
        .load-box {
          display: inline-block;
          width: 30px;
          height: 30px;
          position: relative;
          border: 4px solid #fff;
          top: 50%;
          animation: loader 2s infinite ease;
          box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
          .box-inner {
            vertical-align: top;
            display: inline-block;
            width: 100%;
            background-color: #1976d2;
            animation: loader-inner 2s infinite ease-in;
          }
        }
      }
      .text-holder {
        color: #ffffff;
        text-align: center;
        h2 {
          font-weight: 700;
          text-shadow: 2px 2px 4px rgb(0 0 0 / 29%);
        }
        h4 {
          font-weight: 400;
          text-shadow: 2px 2px 4px rgb(0 0 0 / 29%);
        }
      }
    }
  }
}

@keyframes bounceInDown {
  0%,
  60%,
  75%,
  90%,
  100% {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  0% {
    opacity: 0;
    transform: translate3d(0, -3000px, 0);
  }
  60% {
    opacity: 1;
    transform: translate3d(0, 25px, 0);
  }
  75% {
    transform: translate3d(0, -10px, 0);
  }
  90% {
    transform: translate3d(0, 5px, 0);
  }
  100% {
    transform: none;
  }
}
@keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(180deg);
  }
  50% {
    transform: rotate(180deg);
  }
  75% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes loader-inner {
  0% {
    height: 0;
  }
  25% {
    height: 0;
  }
  50% {
    height: 100%;
  }
  75% {
    height: 100%;
  }
  100% {
    height: 0;
  }
}
